
.Post-archive{
	margin-top:20px;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack:distribute;
	    justify-content:space-around;
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
}
.post-archive-body{
	width:45vw;
	margin-bottom:20px;
}

.post-image{
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	width:45vw;
	height:40vw;
	overflow:hidden;
	display:-ms-grid;
	display:grid;
	justify-items:center;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	border:2px solid #707070;
	border-radius:15px 15px 0px 0px;
}
.post-image img{
	width:150%;
}
.price-favorite{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-pack:justify;
	    -ms-flex-pack:justify;
	        justify-content:space-between;
	padding:0px 5px;
}
.price-favorite .Price{
	font-family:sans-serif;
	color:#106CD5;
	font-weight:bold;
	font-size:15px;
}
.post-contents{
	padding-top:5px;
	background:#fff;
	border-right:2px solid #707070;
	border-left:2px solid #707070;
	border-bottom:2px solid #707070;
	border-radius:0px 0px 15px 15px;
}
.post-contents .product{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-pack:center;
	    -ms-flex-pack:center;
	        justify-content:center;
	color:#000000;
	font-weight:bold;
	font-size:14px;

}
.post-contents .add-to-favorite{
	width:10%;
	margin:5px;
}
.post-contents .location {
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	padding:10px;
	-ms-flex-pack:distribute;
	    justify-content:space-around;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
}
.post-contents .location img{
	width:7%;
	opacity:0.7;
	margin-right:20px;
}
.post-contents .location .location-text{
	color:rgba(0,0,0,0.6);
	font-weight:600;
	text-transform:capitalize;
	font-size:12px;

}


/*POST ARCHIVE ENDS HERE*/