/*BROUSE CATOGERY STARTS HERE*/
.brouse-catogery{
	background: #10D5D030;
    width: 100%;
    margin: 0;
    padding: 0;
}
.brouse-catogery .cat-top{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-pack:justify;
	    -ms-flex-pack:justify;
	        justify-content:space-between;
	padding:5px 10px;
}
.brouse-catogery .cat-top .B-cat-text{
	font-family:sans-serif;
	text-align:left;
	font-size:17px;
}
.brouse-catogery .cat-top .B-cat-seeAll{
	font-family:sans-serif;
	font-size:17px;
	font-weight:bold;
	color:blue;
	cursor:pointer;
}
.brouse-catogery .cat-top .B-cat-seeAll:hover{
	text-decoration:underline;
}

		/*CATOGERY ICON STARTS HERE*/
.B-cat{
	display:-ms-grid;
	display:grid;
	-ms-grid-columns:auto auto auto auto;
	grid-template-columns:auto auto auto auto;
	justify-items:center;
}
.B-cat-Icons{
	padding:10px;
	cursor:pointer;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-pack:center;
	    -ms-flex-pack:center;
	        justify-content:center;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	    -ms-flex-direction:column;
	        flex-direction:column;
}
.B-cat-Icons:hover{
	background:red;
}
.B-cat-Icons .image{
	height:10vh;
	width:10vh;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-pack:center;
	    -ms-flex-pack:center;
	        justify-content:center;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	background-color:#FFDE5B;
	border:1px solid #707070;
	border-radius:50%;
}

.B-cat-Icons .image img {
	width:70%;
}
.B-cat .mobiles img{
	width:45%;
}
.B-cat-Icons div{
	color:#1E3BF9;
	font-family:sans-serif;
	font-weight:600;
	font-size:15px;
	text-align:center;
	margin-top:10px;
}

		/*CATOGERY ICON ENDS HERE*/
