/*MOBILE NAV STARTS HERE*/
.m-nav{
	bottom:0;
	position:fixed;
	width:100%;
	background:#E5E5E5;
	display:-webkit-box;
	display:-ms-flexbox;
	display:grid;
	grid-template-columns: 20% 20% 20% 20% 20%;
}
.nav-menu{
	position:relative;
	width:100%;
	height:60px;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	-webkit-box-pack:end;
	    -ms-flex-pack:end;
	        justify-content:flex-end;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	    -ms-flex-direction:column;
	        flex-direction:column;
	cursor:pointer;

}
.nav-menu:hover{
	background:red;
}
.nav-menu img{
	width:35%;
	height:40%;
}
.nav-menu .explore-text{
	font-family:sans-serif;
	font-size:14px;
	text-align:center;
	margin-top:10px;
	font-weight:bold;
	color:rgba(0,0,0,0.8);
}

/*MOBILE NAV ENDS HERE*/