/*TOP LOCATION FIELD STARTS HERE*/
.TopnavMenu{
	z-index:1000;
	width: 100%;
	position:fixed;
	top:0;
}

.top-location{
	background:#C8C2CF;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack:distribute;
	    justify-content:space-around;
	padding:0px 10px;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	height:30PX;
	overflow:hidden;
}
.top-location p{
	font-size:4vw;
	font-family:sans-serif;
	font-weight:400;
	color:#707070;
}
.top-location img{
	width:3vw;
	margin-right:15px;
}
/*TOP LOCATION FIELD ENDS HERE */

/*SEARCH FIELD STARTS HERE*/
.SearchField{
	background:#E5ECE6;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-pack:center;
	    -ms-flex-pack:center;
	        justify-content:center;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	padding:5px 0px;
}
.SearchField .search{

	width:90vw;
	outline:none;
	padding-right:10vw;
	font-family:sans-serif;
	font-size:4vw;
	color:#666;
	font-weight:600;
	height:35px;
	border:1px solid #707070;
	border-radius:15px;
	padding-left: 10vw;
}

/*SEARCH FIELD ENDS HERE*/